import { Component, OnInit } from '@angular/core';
import {ModalController} from "@ionic/angular";
import firebase from "firebase";


import {Citizen} from "../../models/citizen";
import {FirebaseService} from "../../services/firebase.service";
import {CitizenService} from "../../services/citizen.service";




@Component({
  selector: 'app-signup-modal',
  templateUrl: './signup-modal.component.html',
  styleUrls: ['./signup-modal.component.scss'],
})
export class SignupModalComponent implements OnInit {

  constructor(private modalCtrl: ModalController,
              public firebaseService: FirebaseService,
              public citizenService: CitizenService
              ) { }

  ngOnInit() {}

  dismissModal(){
    this.modalCtrl.dismiss();
  }

  // tslint:disable-next-line:variable-name
  onSignUp(name, surname, email, password, sex, birthdate1, address,  city, country){
console.log(birthdate1);
    // tslint:disable-next-line:variable-name
    let birthDate: string;
    const idUser = null;
    const pushid = '42a0e22d-3245-4c29-a11c-75a3ce0180b7';
    const lat = null;
    const lon = null;
    birthDate = birthdate1.toString().slice(0, -13);
    // tslint:disable-next-line:variable-name
    const userDTO = {
      idUser,
      name,
      surname,
      email,
      birthDate,
      sex,
      address,
      city,
      country,
      pushid
    };
    this.citizenService.addCitizen( {lat, lon, userDTO} as Citizen).subscribe(data => {
      console.log(data);
    });
    return firebase.auth().createUserWithEmailAndPassword(email, password)
      .then((result) => {
       window.alert('Correctly registered');
        this.SendVerificationMail();
      }).catch((error) => {
        window.alert(error.message);
      });

  }

  SendVerificationMail() {
    return firebase.auth().currentUser.sendEmailVerification();
  }

}
